import { Component, OnInit } from '@angular/core';
import * as datefns from 'date-fns'; // for works with dates on real data

const intl = new Intl.DateTimeFormat('ru', { year: 'numeric', month: 'numeric', day: 'numeric' });

@Component({
  selector: 'app-line-chart',
  templateUrl: './line-chart.component.html',
  styleUrls: ['./line-chart.component.css']
})

export class LineChartComponent implements OnInit {
  private _balanceData =
                  [{         x: new Date(2018, 0, 1),         y: 1     },   // 2018-01-01
                   {         x: new Date(2018, 4, 1),         y: 1.1   },
                   {         x: new Date(2018, 6, 1),         y: 1.3   },
                   {         x: new Date(2018, 8, 11),        y: 0.8   },
                   {         x: new Date(2018, 9, 20),        y: 1.5   },
                   {         x: new Date(2018, 10, 11),       y: 1.6   },
                   {         x: new Date(2018, 10, 16),       y: 1.4   },
                   {         x: new Date(2018, 10, 17),       y: 1.7   }]; // this data get from db (service) in future
  public lineChartType = 'line';
  public lineChartLegend = true;
  public lineChartLabels = this._balanceData.map(val => intl.format(val.x));
  public lineChartData = [{data: this._balanceData.map(val => val.y),
                           label: 'Balance',
                           fill: false }];
  public lineChartColors:Array<any> = [
                               { // dark grey
                                 backgroundColor: 'rgba(77,83,96,0.2)',
                                 borderColor: 'rgba(77,83,96,1)',
                                 pointBackgroundColor: 'rgba(77,83,96,1)',
                                 pointBorderColor: '#fff',
                                 pointHoverBackgroundColor: '#fff',
                                 pointHoverBorderColor: 'rgba(77,83,96,1)'
                               }
                             ];
  public lineChartOptions = {
    animation: { duration: 1000,  },// general animation time
    legend:    { display:  false }, // remove legend block
    scaleShowVerticalLines: false,
    responsive: true,
    title: {
     display: false,
     text: 'History of Change Balance'
   },
    scales: {
            xAxes: [{
            ticks: {
              min: this.lineChartLabels[0],            // first label X, we can set default settings
              max: this.lineChartLabels[this.lineChartLabels.length - 1]     // last label X
            },
            gridLines: {
              display: false ,  // не показывать секу
            //  color: "#FFFFFF"
            },
            scaleLabel: {
							display: true,
							labelString: 'Time'
						},
            }],
            yAxes: [{
              ticks: {
                    beginAtZero: true,
                    mirror: false,
                },
              gridLines: {
                display: false ,
          //      color: "#FFFFFF"
              },
              scaleLabel: {
  						  display: true,
  							labelString: 'Balance changed %'
  						}
            }]

          }
  };
  public changeZoom(delta):void {

    const minDate = datefns.subDays(datefns.format(new Date(), 'YYYY.MM.DD'), delta);
    console.log(minDate); // check
    const newPoints = this._balanceData.filter(v => v.x > minDate);
    this.lineChartData = [{data: newPoints.map(val => val.y), label: 'Changed', fill: false}]; // это новый уменьшенный массив для значений Y

    setTimeout( () => {                                                // в библиотеке какой-то глюк, только с этим костылём работает
      this.lineChartLabels = newPoints.map(val => intl.format(val.x)); // это новый уменьшенный массив для значений X
    }, 0);
  };

  constructor() { }

  ngOnInit() { }
}
